header {
    position: absolute;
    width: 100%;
    background: rgba(51, 51, 51, 0.5); }
hr {
    border-bottom: 1px solid #FFFFFF; }
.header__dop {
    display: flex;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 18px;
    padding-bottom: 18px;
    justify-content: space-between;
    .header__logo {
        font-weight: 600;
        font-size: 40px;
        line-height: 49px;
        color: #FFFFFF;
        &:hover {
            color: $main; } }
    .header__media {
        display: flex;
        gap: 30px;
        margin-left: 75px;
        img {
            max-width: 35px;
            max-height: 35px; } }
    .header__adress {
        color: white;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        text-align: center;
        max-width: 375px;
        width: 100%; }
    .header__number {
        a {
            color: white;
            font-weight: 500;
            font-size: 25px;
            line-height: 29px;
            text-align: center;
            .header__number-phone {
                text-align: center;
                &:hover {
                    color: $main; } }
            .header__number-text {
                text-align: center;
                font-weight: 600;
                font-size: 14px;
                line-height: 17px;
                color: rgba(255, 255, 255, 0.8); } } }
    .header__button {
        max-width: 273px;
        height: 52px;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px; } }
.navbar {
    padding-left: 388px;
    padding-right: 388px;
    ul {
        padding-top: 23px;
        padding-bottom: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        li {
            a {
                font-weight: 500;
                font-size: 23px;
                line-height: 27px;
                color: white;
                &:hover {
                    color: $main; } } } } }
.main-menu-btn-icon {
    display: none; }
#menu-bar {
	display: none; }
.header__menu > .main-menu-btn-icon,
.header__menu > .main-menu-btn-icon:before,
.header__menu > .main-menu-btn-icon:after {
    position: absolute;
    left: 15px;
    top: 125px;
    height: 4px;
    width: 30px;
    background: #589DE9;
    -webkit-transition: all 0.25s;
    transition: all 0.25s; }
.header__menu > .main-menu-btn-icon:before {
    content: '';
    top: -10px;
    left: 0; }
.header__menu > .main-menu-btn-icon:after {
    content: '';
    top: 10px;
    left: 0; }
.header__menu > #menu-bar:checked ~ .main-menu-btn-icon {
    height: 0;
    background: inherit;
    -webkit-transition: all 0.5s;
    transition: all 0.5s; }
.header__menu > #menu-bar:checked ~ .main-menu-btn-icon:before {
    transform: rotate(-45deg);
    top: 0;
    -webkit-transition: all 0.5s;
    transition: all 0.5s; }
.header__menu > #menu-bar:checked ~ .main-menu-btn-icon:after {
    top: 0;
    transform: rotate(45deg);
    -webkit-transition: all 0.5s;
    transition: all 0.5s; }
.header__menu > .navbar ul li .drop:after {
    content: '';
    display: block;
    position: absolute;
    right: 0px;
    top: 42%;
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 10px solid #333;
    border-bottom: 0px solid transparent; }
.header__menu > .navbar ul li ul li .drop:after {
    content: '';
    display: block;
    position: absolute;
    right: 20px;
    top: 42%;
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 10px solid #333;
    border-bottom: 0px solid transparent; }
.header__menu > .navbar ul li ul li:focus-within > .drop:after,
.header__menu > .navbar ul li ul li:active > .drop:after,
.header__menu > .navbar ul li ul li:target > .drop:after,
.header__menu > .navbar ul li ul li:hover > .drop:after {
    content: '';
    display: block;
    position: absolute;
    right: 20px;
    top: 42%;
    width: 0;
    height: 0;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 10px solid white;
    border-bottom: 0px solid transparent; }
@media(max-width: 834px) {
	.header__menu > label {
        display: initial; }
	.header__menu > .navbar {
        max-height: 650px;
        z-index: 10;
        overflow: auto;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        background: white;
        border-top: 1px solid rgba(0,0,0,.1);
        display: none; }
	.header__menu > .navbar ul li {
        width: 100%; }
	.header__menu > .navbar ul li ul {
        position: relative;
        width: 100%; }
	.header__menu > .navbar ul li ul li {
        background: white; }
	.header__menu > .navbar ul li ul li a {
        padding-left: 45px; }
	.header__menu > .navbar ul li ul li:before {
		content: '';
		display: block {
	    position: absolute;
	    width: 15px;
	    height: 2.5px;
	    left: 20px;
	    top: 50%;
	    background: #333; } }
	.header__menu > .navbar ul li ul li ul {
		width: 100%;
		left: 0; }
	.header__menu > #menu-bar:checked ~ .navbar {
		display: initial; }
	.header__menu > .navbar ul li .drop:after {
		content: '';
		display: block {
	    position: absolute;
	    right: 20px;
	    top: 33%;
	    width: 0; }
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-top: 15px solid #333;
		border-bottom: 0px solid transparent; }
	.header__menu > .navbar ul li ul li .drop:after {
		content: '';
		display: block {
	    position: absolute;
	    right: 20px;
	    top: 33%;
	    width: 0; }
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-top: 15px solid #333;
		border-bottom: 0px solid transparent; }
	.header__menu > .navbar ul li:focus-within > .drop:after,
	.header__menu > .navbar ul li:active > .drop:after,
	.header__menu > .navbar ul li:target > .drop:after,
	.header__menu > .navbar ul li:hover > .drop:after {
		content: '';
		display: block {
	    position: absolute;
	    right: 20px;
	    top: 13%;
	    width: 0; }
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-top: 15px solid white;
		border-bottom: 0px solid transparent; }
	.header__menu > .navbar ul li ul li:focus-within > .drop:after,
	.header__menu > .navbar ul li ul li:active > .drop:after,
	.header__menu > .navbar ul li ul li:target > .drop:after,
	.header__menu > .navbar ul li ul li:hover > .drop:after {
		content: '';
		display: block {
	    position: absolute;
	    right: 20px;
	    top: 10%;
	    width: 0; }
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-top: 15px solid white;
		border-bottom: 0px solid transparent; } }
