// main page elements
body {
	font-size: 14px;
	color:#333333 {}
	font-family: 'Gilroy';
	font-style: normal;
	font-weight: 400;
	background: #EEF4F2;
	overflow-x: hidden;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	max-width: 1680px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1200px) {
		padding: 0 15px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 10px; } }
.white-popup {
	position: relative;
	background-image: url(../img/Rectangle.png);
	background-repeat: no-repeat;
	width: auto;
	max-width: 832px;
	width: 100%;
	height: 660px;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	padding-top: 50px;
	padding-left: 94px;
	padding-right: 94px;
	h2 {
		text-align: center;
		font-weight: 500;
		font-size: 30px;
		line-height: 35px;
		color: white;
		margin-bottom: 30px; }
	form {
		gap: 30px;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		margin-bottom: 50px;
		a {
			display: flex;
			align-items: center;
			justify-content: center;
			font-weight: 500;
			font-size: 20px;
			line-height: 24px;
			height: 60px;
			width: 100%;
			margin-top: 20px;
			background: #589DE9;
			color: white;
			border: 2px solid #589DE9; }
		input {
			text-align: center;
			width: 100%;
			height: 60px;
			border: 2px solid #FFFFFF;
			background: inherit;
			color: white;
			&::-moz-placeholder {
				color: white; }
			&::-webkit-input-placeholder {
				color: white; } } } }
.main__btn {
	background: $main;
