.container {
	position: relative; }
.swiper-dots {
	bottom: 70px;
	position: absolute;
	text-align: center;
	left: 0;
	right: 0;
	justify-content: center;
	display: flex;
	gap: 25px;
	.swiper-pagination-bullet {
		width: 14px;
		height: 14px;
		opacity: 1;
		background: inherit;
		&:after {
			content: '';
			width: 20px;
			height: 20px;
			margin-left: -11px;
			margin-top: -4px;
			border: 1px solid white;
			border-radius: 50%;
			position: absolute; } }
	.swiper-pagination-bullet-active {
		background: $main; } }
